.package {
  margin-right: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px;
  padding-top: 40px;
  width: 370px;
  background: #FFFFFF;
  border: 1px solid fade-out($darkgrey, 0.7);
  border-radius: 6px;

  &:hover {
    border: 3px solid $maincolor;

    @media (min-width: $desktop-width-only) {
      padding: 28px;
      padding-top: 38px;
    }
  }

  &:hover .package__header {
    color: $maincolor;
  }

  &:hover .package__icon {
    fill: $darkgrey;
  }

  &:hover .package__text {
    border-bottom: 1px solid $maincolor;
  }

  @media (max-width: $tablet-width) {
    margin-right: 50px;
    padding: 0;
    padding-top: 30px;
    width: 288px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 30px;
    margin-right: 0;
  }
}

.package:last-child {
  margin-right: 0;
}

.package__header {
  margin-bottom: 36px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  color: fade-out($lightgrey, 0.3);

  @media (max-width: $tablet-width) {
    margin-bottom: 11px;
    flex-direction: column;
    align-items: center;
  }
}

.package__title {
  margin: 0;
  font-weight: 600;
  font-size: 27px;
  line-height: 38px;

  @media (max-width: $tablet-width) {
    margin-bottom: 11px;
    font-size: 24px;
    line-height: 34px;
  }
}

.package__cost {
  margin: 0;
  font-weight: bold;
  font-size: 25px;
  line-height: 34px;
}

.package__text {
  margin: 0;
  margin-bottom: 30px;
  padding-right: 16px;
  padding-bottom: 28px;
  border-bottom: 1px solid fade-out($lightgrey, 0.7);
  color: $lightgrey;

  @media (max-width: $tablet-width) {
    margin: 0 20px;
    margin-bottom: 20px;
    padding: 0 10px;
    padding-bottom: 8px;
    text-align: center;
  }
}

.package__list {
  margin: 0;
  margin-bottom: auto;
  padding-left: 29px;
  padding-right: 42px;
  list-style: none;

  @media (max-width: $tablet-width) {
    padding-left: 50px;
    padding-right: 15px;
  }
}

.package__item {
  position: relative;
  margin-bottom: 15px;
  line-height: 32px;

  @media (max-width: $tablet-width) {
    margin-bottom: 9px;
  }
}

.package__icon {
  position: absolute;
  top: 10px;
  left: -30px;
  fill: $maincolor;
}

.package__button {
  margin-top: 15px;

  @media (max-width: $tablet-width) {
    margin-top: 25px;
    padding: 19px;
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
  }

  @media (max-width: $mobile-width) {
    margin-top: 20px;
  }
}
